}

h1, h2, h3, h4, h5, p, ul, li {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
}

body {
  color: #241E20;
  background-color: #fff;
  font-family: 'Inter', sans-serif;
  font-size: 16px;
  line-height: 1.25em;
  font-weight: bold;
}

a {
  color: #241E20;
  text-decoration: none;
  opacity: 1;
  transition: all 0.3s;
}

a:not(.logo):hover {
  opacity: 0.8;
}

h1,
h2 {
  position: relative;
  max-width: 1000px;
  margin: 0 auto;
  padding-bottom: 15px;
  font-size: 2.25em;
  line-height: 1.25em;
  color: #DFE5EB;
  text-transform: uppercase;
  text-align: center;
}

h1::before,
h2::before {
  content: '';
  display: block;
  width: 90%;
  max-width: 443px;
  height: 6px;
  position: absolute;
  bottom: 0;
  left: 50%;
  background-color: #F41C24;
  transform: translateX(-50%);
}

h1 {
  margin-bottom: 10px;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  background: url('../img/main-bg.png') bottom center no-repeat;
}

.contacts {
  display: flex;
  align-items: center;
  align-self: center;
  width: 100%;
  max-width: 828px;
}

.contacts p  {
  font-size: 2em;
  line-height: 1.25em;
}

.contacts li {
  position: relative;
  width: 50%;
}

.contacts li:first-child {
  padding-right: 35px;
}

.contacts li:first-child::before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  right: -3px;
  width: 6px;
  height: 96%;
  background-color: #F41C24;
  transform: translateY(-50%);
}

.contacts li:last-child {
  padding-left: 35px;
}

.main-header .contacts {
  max-width: 494px;
}

.main-header .contacts p {
  font-size: 1.5em;
  line-height: 1.25em;
}

.main-header .contacts li:first-child {
  padding-right: 15px;
  width: 62%;
}
.main-header .contacts li:last-child {
  padding-left: 15px;
  width: 38%;
}

.main-header {
  padding: 25px 50px;
  display: flex;
  justify-content: space-between;
  flex-flow: row wrap;
  align-items: center;
}

.logo {
  display: block;
  width: 197px;
  height: 110px;
  background: url('../img/logo.png') no-repeat;
  background-size: cover;
}

.main-footer {
  padding: 36px 15px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.main > * {
  padding: 0 10px;
}

.main-slide {
  padding: 584px 15px 126px;
  text-align: center;
  background: url('../img/main.jpg') no-repeat;
  background-position: top center;
}

.main-slide > * {
  color: #DFE5EB;
}

.partners ul {
  display: flex;
  padding: 15px 0;
  align-items: center;
  justify-content: space-around;
}

.partners li {
  width: 25%;
  padding: 0 10px;
  text-align: center;
}

.partners img {
  width: auto;
  height: auto;
  max-width: 100%;
}

.gallery {
  padding: 42px 15px 0 15px;
  background: url('../img/gallery-bg.png') top center no-repeat;
}

.gallery .contacts {
  margin: 0 auto 170px;
}

.gallery h2 {
  margin-bottom: 130px;
}

.gallery-list {
  display: flex;
  flex-flow: row wrap;
  max-width: 1250px;
  margin: 0 auto;
}

.gallery-list li {
  width: 50%;
  margin-bottom: 130px;
  padding-right: 10px;
}

.gallery-list li:nth-child(2n) {
  text-align: right;
  padding-right: 0;
  padding-left: 10px;
}

.gallery-list img {
  width: 100%;
  max-width: 516px;
  height: auto;
}

@media screen and (max-width: 1024px) {
  body {
    font-size: 14px;
  }

  .main-header {
    padding: 10px 15px;
  }
}

@media screen and (max-width: 810px) {
  h1,
  h2 {
    max-width: 640px;
  }

  .container {
    background: none;
  }

  .main-header .contacts {
    max-width: 440px;
  }

  .main-slide {
    padding-top: 300px;
  }

  .gallery h2,
  .gallery-list li,
  .gallery .contacts {
    margin-bottom: 90px;
  }
}

@media screen and (max-width: 640px) {
  h1,
  h2 {
    font-size: 1.714em;
  }

  .logo {
    width: 138px;
    height: 77px;
    margin: 0 auto 10px;
  }

  .contacts p {
    font-size: 1em;
  }

  .contacts li:first-child {
    padding-right: 10px;
    width: 62%;
    text-align: right;
  }

  .contacts li:last-child {
    padding-left: 10px;
    width: 38%;
  }

  .main-header {
    flex-direction: column;
  }

  .main-header .contacts {
    margin: 0 auto;
    max-width: 340px;
  }

  .main-header .contacts p {
    font-size: 1em;
  }

  .main-header .contacts li:first-child {
    padding-right: 10px;
    text-align: right;
  }

  .main-header .contacts li:last-child {
    padding-left: 10px;
  }

  .main-slide {
    padding-top: 240px;
  }

  .gallery h2,
  .gallery-list li,
  .gallery .contacts {
    margin-bottom: 50px;
  }

  .gallery-list li {
    width: 100%;
    padding: 0 !important;
  }
}
